.form__submit {
    font-family: 'Inter', Arial, Helvetica, sans-serif;
    font-size: 18px;
    font-weight: normal;
    line-height: 22px;
    text-align: center;
    color: #fff;
    background-color: #000;
    border: 0px;
    border-radius: 2px;
    width: 100%;
    height: 50.15px;
    margin: 30px 0 0;
    opacity: 1;
    transition: opacity 0.2s ease-in-out;
    cursor: pointer;
}

.form__submit:hover {
    opacity: 0.8;
}
