.profile {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    color: #fff;
    padding: 0;
    margin: 0 0 52px;
    justify-content: space-between;
}

@media screen and (max-width: 919px) {
    .profile {
        justify-content: center;
    }
}

@media screen and (max-width: 636px) {
    .profile {
        margin: 0 0 36px;
        flex-direction: column;
    }
}
