.photo-card__title {
    font-family: 'Inter', Arial, Helvetica, sans-serif;
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    color: #000;
    justify-self: stretch;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    grid-row: span 2;
    margin: 0px;
    padding: 0px;
}
